
import './index.css';
import { Suspense, useRef, useState } from 'react'
import { Canvas } from '@react-three/fiber';
import { OrbitControls, useGLTF } from '@react-three/drei';


function Model(props) {
  const { nodes, materials } = useGLTF('/shoe.gltf')
  return (
    <group {...props} dispose={null} scale={3}>
      <mesh geometry={nodes.shoe.geometry} material={materials.laces} material-color="red" />
      <mesh geometry={nodes.shoe_1.geometry} material={materials.mesh} material-color={props.mesh} />
      <mesh geometry={nodes.shoe_2.geometry} material={materials.caps} material-color="blue" />
      <mesh geometry={nodes.shoe_3.geometry} material={materials.inner} material-color="blue" />
      <mesh geometry={nodes.shoe_4.geometry} material={materials.sole} material-color={props.sole} />
      <mesh geometry={nodes.shoe_5.geometry} material={materials.stripes} material-color={props.stripes} />
      <mesh geometry={nodes.shoe_6.geometry} material={materials.band} material-color="blue" />
      <mesh geometry={nodes.shoe_7.geometry} material={materials.patch} material-color="blue" />
    </group>
  )
}

function App() {
  const [mesh, setMesh] = useState('#ffffff')
  const [stripes, setStripes] = useState('#ffffff')
  const [sole, setSole] = useState('#ffffff')
  
  return (
    <div className="App">
      <div class="wrapper">
        <div class="card">
            <div class="product-canvas">
              <Canvas>
                <Suspense fallback={null}>
                  <ambientLight />
                  <pointLight position={[10, 10, 10]} intensity={1.5}/>
                  <spotLight intensity={0.11} angle={0.1} />
                  <Model mesh={mesh} stripes={stripes} sole={sole} />     
                  <OrbitControls />             
                </Suspense>
              </Canvas>
            </div>
            <h2>Color chooser</h2>
            <div class='colors'>
                 <div>
                    <input type="color" id="main" name="main" value="#e66465" onChange={(e) => setMesh(e.target.value)}/>
                    <label for="head">Main</label>
                  </div>
                <div>
                    <input type="color" id="stripes" name="stripes" value="#f6b73c" onChange={(e) => setStripes(e.target.value)}/>
                    <label for="body">Stripes</label>
                </div>
                 <div>
                    <input type="color" id="sole" name="sole" value="#f6b73c" onChange={(e) => setSole(e.target.value)}/>
                    <label for="body">sole</label>
                </div>
            </div>
        </div>
    </div>
    </div>
  );
}

export default App;
